<template><section><h1>TiledCacheImageryProvider</h1>
<p>The <code v-pre>vc-provider-imagery-tiledcache</code> component is used to load the TiledCache image service.</p>
<h2>Example</h2>
<h3>Load an imagerylayer with TiledCacheImageryProvider</h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer" ref="viewerContainer">
      <vc-viewer @ready="ready" fullscreenButton :fullscreenElement="fullscreenElement">
        <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast">
          <vc-provider-imagery-tiledcache url="https://songluck.com/gis/TiledCacheService/TiledCacheServlet" dir="WhiteMap"></vc-provider-imagery-tiledcache>
        </vc-layer-imagery>
        <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast">
          <vc-provider-imagery-tiledcache url="https://songluck.com/gis/TiledCacheService/TiledCacheServlet" dir="Labels"></vc-provider-imagery-tiledcache>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>alpha</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>brightness</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>contrast</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
      </div>
    </div>
  </template>
  
</doc-preview>
