<h2>属性</h2>
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre>required</code> TiledCache 影像服务地址。</td>
</tr>
<tr>
<td>dir</td>
<td>String</td>
<td></td>
<td><code v-pre>required</code> 指定目录。</td>
</tr>
<tr>
<td>scales</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code> 指定地图各级比例尺。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例。</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>当图层的提供者发生异步错误时触发, 返回一个 TileProviderError 实例。</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>当图层可用时触发, 返回 ImageryProvider 实例。</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          alpha: 1,
          brightness: 1,
          contrast: 1,
          fullscreenElement: document.body
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.cesiumInstance = cesiumInstance
          this.fullscreenElement = this.$refs.viewerContainer
        }
      }
    }
  </script>